import { getUser,setUser } from '@/api/user'
const Cookie = process.client ? require('js-cookie') : undefined
export default {
    // 在路由匹配组件之前会先执行中间件处理
    middleware: 'authenticated',
    async mounted () {
      // 获取用户数据
      const { data } = await getUser()
      // 因为不会显示密码，初始化一个空密码
      data.password = ''
      this.user = data.user
    },
    data () {
      return {
        user: {},
        btnDisabled: false
      }
    },
    methods: {
      async logout () {
        // 清除 cookie 缓存
        Cookie.remove('user')
        this.$store.commit('logout')
        this.$router.push('/')
      },
      async setUserInfo () {
        // 可能更新失败
        try {
          // 禁用按钮
          this.btnDisabled = true
          // 更新数据接口
          const { data } = await setUser({
            user: this.user
          })
          // 成功更新后跳转到用户详情页
          this.$router.push(`/profile/${data.user.username}`)
          this.btnDisabled = false
        } catch(err) {
          console.error(err)
        }
      }
    }
}
